.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

input[type=text] {
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

button {
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

button:hover {
  opacity: 0.5;
}

table {
  width: 90%;
  margin: 5%;
}

table .student-name {
  background-color: #bebebe;
  padding: 10px;
}

table a {
  display: block;
  text-decoration: none;
  color: #000000
}

table a:hover {
  opacity: 0.5;
}

table th {
  border: 1px solid #000000;
  margin: 0px;
  padding: 10px;
}

table td {
  border: 1px solid #000000;
  margin: 0px;
  padding: 10px;
}

table .todo {
  background-color: #4b4b4b;
}

table .checked, table .done{
  background-color: #359c3a;
}

table [class="in progress"] {
  background-color: #9da000;
}

table .checking {
  background-color: #dd4949;
}

table .fail {
  background-color: #b80000;
}
